import { C } from "./Constants";
import { Manage } from "./Manage";
import { Lot } from "./Lot";
import { Payment } from "./Payment";
import { Driver } from "./Driver";

export class Request extends Manage {
  constructor({ lotUID, paymentUID, start, end, spotType, uid } = {}) {
    super(uid);
    this.lotUID = lotUID;
    this.paymentUID = paymentUID;
    this.state = C.REQUEST_PENDING;
    this.spotType = spotType || C.SPOT_TYPE_STANDARD;
    this.ORM = C.ORM_REQUESTS;
    this.start = new Date(start).toString();
    this.end = new Date(end).toString();
  }

  modify({ state } = {}) {
    this.state = state || this.state;
    return this.put({ uid: this.uid });
  }

  send() {
    return this.post();
  }

  accept() {
    return this.modify({ state: C.REQUEST_ACCEPTED }).then((res) => res);
  }

  reject() {
    return this.modify({ state: C.REQUEST_REJECTED }).then((res) => res);
  }

  automate() {
    return new Promise((resolve, reject) => {
      new Lot()
        .get({ uid: this.lotUID })
        .then((lot) => {
          const num = lot.isTypeAvailable({ type: this.spotType });
          if (num) {
            resolve(this.accept());
          } else {
            resolve(this.reject());
          }
        })
        .catch(() => resolve(null));
    });
  }

  cost() {
    return (
      (new Date(this.end).getTime() - new Date(this.start).getTime()) *
      C.COST_BY_MILLISECONDS
    );
  }

  checkIn() {
    this.state = C.REQUEST_IN_USE;
    return new Promise((resolve, reject) => {
      this.modify()
        .then((request) => {
          new Lot()
            .get({ uid: request.lotUID })
            .then((lot) => resolve(lot.occupyRandomSpot()));
        })
        .catch((error) => reject(error));
    });
  }

  checkOut() {
    this.state = C.REQUEST_EXPIRED;
    return new Promise((resolve, reject) => {
      this.modify()
        .then((request) => {
          new Lot()
            .get({ uid: request.lotUID })
            .then((lot) => resolve(lot.releaseRandomSpot()));
        })
        .catch((error) => reject(error));
    });
  }

  toString() {
    return new Promise((resolve) => {
      Promise.all([
        new Payment()
          .get({ uid: this.paymentUID })
          .then((payment) =>
            new Driver()
              .get({ uid: payment.driverUID })
              .then((driver) => driver)
          ),
        new Lot().get({ uid: this.lotUID }).then((lot) => lot),
      ])
        .then(([driver, lot]) => {
          resolve(
            `${driver.name.toUpperCase()} - Requested to Park at ${
              lot.name
            } (${this._formatDate(new Date(this.start))} - ${this._formatDate(
              new Date(this.end)
            )})`
          );
        })
        .catch(() => resolve("Error!! request object invalid"));
    });
  }

  _formatDate(date) {
    const padTo2Digits = (num) => num.toString().padStart(2, "0");
    return (
      [
        date.getFullYear(),
        padTo2Digits(date.getMonth() + 1),
        padTo2Digits(date.getDate()),
      ].join("-") +
      " " +
      [
        padTo2Digits(date.getHours()),
        padTo2Digits(date.getMinutes()),
        // padTo2Digits(date.getSeconds()),
      ].join(":")
    );
  }
}
